body, html, #root {
    height: 100%;
    margin: 0;
    font-family: Arial, sans-serif;
}

.container {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.main-content {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f0f0f0;
}

.footer {
    background-color: #333;
    color: white;
    padding: 10px;
    display: flex;
    justify-content: flex-end;
}

.footer button {
    margin-left: 10px;
    padding: 10px 20px;
    background-color: #444;
    color: white;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;
}

.footer button:hover {
    background-color: #555;
}
